import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {DrawerService} from './drawer.service';
import {SharedModule} from '@gaxon/modules';
import {BlankComponent} from './blank/blank.component';
import { NgxMasonryModule } from 'ngx-masonry';

const appsRoutes: Routes = [

  // {
  //   path: 'filling',
  //   loadChildren: () => import('./filling/filling.module').then(m => m.FillingModule)
  // },
  {
    path: 'main-block',
    loadChildren: () => import('./main-block/main-block.module').then(m => m.MainBlockModule)
  },
  {
    path: 'tickets',
    loadChildren: () => import('./mail/mail.module').then(m => m.MailModule)
  },
  {
    path: 'ticket',
    loadChildren: () => import('./ticket-managment/ticket-managment.module').then(m => m.TicketManagmentModule)
  },
  {
    path: 'master',
    loadChildren: () => import('./master-data/master-data.module').then(m => m.MasterDataModule)
  },
  {
    path: '',
    loadChildren: () => import('./zoho/zoho.module').then(m => m.ZohoModule)
  },

];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(appsRoutes),
    NgxMasonryModule
  ],
  declarations: [ 
    BlankComponent
    
  ],
  providers: [
    DrawerService
  ]
})
export class PagesModule {
}
